import React, { Component, Fragment } from 'react';
import CustomsBankCode from './CustomsBankCode';
import BankCode from './BankCode';
import BankBranchCode from './BankBranchCode';
import BankAccountNumber from './BankAccountNumber';
import TotalPaymentAmount from './TotalPaymentAmount';
import TotalTax from './TotalTax';
import TotalDeposit from './TotalDeposit';
import FieldHeader from '../../../shared/FieldHeader';

class BankFields extends Component {
  constructor(props) {
    super(props);
    this.state = {
      customsBankCode: '',
      bankCode: '',
      bankBranchCode: '',
      bankAccountNumber: '',
      totalPaymentAmount: '',
      totalTax: '',
      totalDeposit: '',
    };
    this.shouldBeVisible = this.shouldBeVisible.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }

  handleChange(event) {
    this.setState({
      [event.target.name]: event.target.value,
    });
  }

  shouldBeVisible() {
    const { guaranteeMethod, paymentMethod, guaranteeType } = this.props;

    if (
      paymentMethod === 'H' ||
      (guaranteeMethod === 'H' && guaranteeType === 'C')
    ) {
      return true;
    } else {
      return false;
    }
  }

  render() {
    const {
      customsBankCode,
      bankCode,
      bankBranchCode,
      bankAccountNumber,
      totalPaymentAmount,
      totalTax,
      totalDeposit,
    } = this.state;

    const { handleChange } = this;
    return this.shouldBeVisible() ? (
      <Fragment>
        <FieldHeader>การชำระค่าภาษี</FieldHeader>
        <CustomsBankCode
          handleChange={handleChange}
          customsBankCode={customsBankCode}
        />
        <BankCode handleChange={handleChange} bankCode={bankCode} />
        <BankBranchCode
          handleChange={handleChange}
          bankBranchCode={bankBranchCode}
        />
        <BankAccountNumber
          handleChange={handleChange}
          bankAccountNumber={bankAccountNumber}
        />
        <TotalPaymentAmount
          handleChange={handleChange}
          totalPaymentAmount={totalPaymentAmount}
        />
        <TotalTax handleChange={handleChange} totalTax={totalTax} />
        <TotalDeposit
          handleChange={handleChange}
          totalDeposit={totalDeposit}
        />
      </Fragment>
    ) : (
      ''
    );
  }
}

export default BankFields;
