import React, { Component, Fragment } from 'react';
import {
  FormControlLabel,
  FormControl,
  FormLabel,
  Radio,
  RadioGroup,
  Collapse,
} from '@material-ui/core';
import ApprovalPort from './ApprovalPort';
import ApprovalNumber from './ApprovalNumber';

class ApprovalPortRadio extends Component {
  state = {
    value: '',
    checked: false,
  };

  handleChange = event => {
    const checkedValue = event.target.value;
    this.setState({ value: checkedValue });
    checkedValue === 'Y'
      ? this.setState({
          checked: true,
        })
      : this.setState({
          checked: false,
        });
  };

  render() {
    return (
      <Fragment>
        <FormControl
          fullWidth
          component="fieldset"
          style={{ margin: 8, marginTop: 25 }}
        >
          <FormLabel component="legend">
            ใช้สิทธิประโยชน์ใบขนสินค้าหลายเที่ยวเรือ
          </FormLabel>
          <RadioGroup
            name="useMultipleShipments"
            value={this.state.value}
            onChange={this.handleChange}
          >
            <FormControlLabel value="Y" control={<Radio />} label="ใช้" />
            <FormControlLabel
              value="N"
              control={<Radio />}
              label="ไม่ใช้"
            />
          </RadioGroup>
        </FormControl>

        <Collapse in={this.state.checked}>
          <Fragment>
            <ApprovalPort />
            <ApprovalNumber />
          </Fragment>
        </Collapse>
      </Fragment>
    );
  }
}

export default ApprovalPortRadio;
